const theme = window?.PloverEditor?.theme ?? {};

const getPloverThemeSettings = (path) => {
    const steps = path.split('.');
    let result = theme?.settings ?? {};
    for (let i = 0; i < steps.length; i++) {
        result = result[steps[i]] ?? undefined;
        if (!result) {
            return result;
        }
    }

    return result;
}

export {
    getPloverThemeSettings
}
