import {useSetting as useBlockSetting, useSettings as useBlockSettings} from '@wordpress/block-editor';

/**
 * Polyfill for useSettings before WordPress v6.5.0
 *
 * @param path
 * @returns {*|*[]}
 */
export function useSettings(...path) {
    if (useBlockSettings) {
        return useBlockSettings(...path);
    }

    let result = [];
    path.forEach(item => {
        result.push(useBlockSetting(item));
    })

    return result;
}
