const blockExtensions = window?.PloverEditor?.extensions ?? {};

const getExtension = (extension, default_value = null) => {
    return blockExtensions?.[extension] ?? default_value;
}

const getExtensionSetting = (extension, setting, default_value = null) => {
    return blockExtensions?.[extension]?.[setting] ?? default_value;
}

const hasExtension = (extension) => {
    return blockExtensions?.[extension] ?? false;
}

export {
    getExtension,
    getExtensionSetting,
    hasExtension,
    blockExtensions
}
