import { clone } from 'lodash';

/**
 * Responsive value must necessarily have the desktop key attached to it
 */
export const promoteScalarValueIntoResponsive = (value, fill = false) => {
  let valueWithResponsive = (typeof value === 'object' && Object.keys(value).indexOf('desktop') > -1)
    ? (clone(value) ?? '')
    : {
      desktop: clone(value) ?? '',
      tablet: '__INITIAL_VALUE__',
      mobile: '__INITIAL_VALUE__',
    };

  if (fill) {
    if (valueWithResponsive['tablet'] === '__INITIAL_VALUE__') {
      valueWithResponsive['tablet'] = clone(valueWithResponsive['desktop']);
    }

    if (valueWithResponsive['mobile'] === '__INITIAL_VALUE__') {
      valueWithResponsive['mobile'] = clone(valueWithResponsive['tablet']);
    }
  }

  return valueWithResponsive;
};

/**
 * @param value
 * @param device
 * @returns {*|string}
 */
export const getScalarValueByDevice = (value, device = 'desktop') => {
  return promoteScalarValueIntoResponsive(value, true)[device];
};
