const blockSupports = window?.PloverEditor?.blockSupports ?? {};
const customBlockSupports = window?.PloverEditor?.customBlockSupports ?? {};

const getCustomBlockSupportConfig = (support, default_value = null) => {
  return customBlockSupports?.[support] ?? default_value;
};

const getPloverSupport = (name, support, default_value = null) => {
  return blockSupports?.[name]?.[support] ?? default_value;
};

const hasPloverSupport = (name, support) => {
  return blockSupports?.[name]?.[support] ?? false;
};

export {
  getCustomBlockSupportConfig,
  getPloverSupport,
  hasPloverSupport,
  blockSupports
};
